
@import url(~slick-carousel/slick/slick-theme.css);
@import url(~slick-carousel/slick/slick.css);
body {
  background-image: url("/static/img/background.jpg");
}
img {
  width: 100%;
}
.product-image {
  width: 100%;
  height: 400px;
  background-size: cover;
  background-position: center;
}
.product-container {
  padding: 20px 10px;
}
.subtitle {
  border: 1px solid black;
  margin: 2rem 0;
  border-right: 0;
  padding: 0.5rem 0;
  border-left: 0;
  font-size: 24px;
}
.subtitle-contact {
  color: white;
  border-color: white;
}
.contact {
  background-color: #14213D;
  padding: 20px;
}
.contact .contact-content {
    padding: 1%;
    color: white;
    text-align: center;
    diplay: flex;
    vertial-align: center;
}
.contact .contact-content iframe {
      width: 100%;
      height: 300px;
}
.hunggwo-navbar {
  background: transparent;
  border-radius: 0px;
}
.hunggwo-navbar a {
    color: black;
}
.hunggwo-navbar-bottom {
  background: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.hunggwo-navbar-bottom .container {
    margin: auto;
}
.hero {
  position: relative;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.inner-circle {
  background: #65BDBC;
  width: 80%;
  height: 80%;
  border-radius: 50%;
  margin: auto;
  z-index: 10;
  -webkit-animation: ripple 0.7s linear infinite;
          animation: ripple 0.7s linear infinite;
}
.outter-circle {
  background: transparent;
  width: 700px;
  height: 700px;
  border-radius: 50%;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  top: -10%;
  left: calc(50% - 350px);
  z-index: 0;
  margin: auto;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#hunggwo-logo {
  z-index: 100;
  position: absolute;
  width: 50%;
  left: 25%;
  top: 10%;
}
.introduction {
  position: absolute;
  z-index: 101;
  top: 78%;
  background: #E8C862;
  padding: 10px;
}
.introduction h2 {
    color: white;
    font-weight: 200;
    margin-top: 10px;
}
.introduction hr {
    margin: 0px;
}
@-webkit-keyframes ripple {
0% {
    box-shadow: 0 0 0 0 rgba(101, 189, 188, 0.3), 0 0 0 2em rgba(101, 189, 188, 0.3), 0 0 0 4em rgba(101, 189, 188, 0.3), 0 0 0 6em rgba(101, 189, 188, 0.3);
}
100% {
    box-shadow: 0 0 0 2em rgba(101, 189, 188, 0.3), 0 0 0 4em rgba(101, 189, 188, 0.3), 0 0 0 6em rgba(101, 189, 188, 0.3), 0 0 0 9em rgba(101, 189, 188, 0);
}
}
@keyframes ripple {
0% {
    box-shadow: 0 0 0 0 rgba(101, 189, 188, 0.3), 0 0 0 2em rgba(101, 189, 188, 0.3), 0 0 0 4em rgba(101, 189, 188, 0.3), 0 0 0 6em rgba(101, 189, 188, 0.3);
}
100% {
    box-shadow: 0 0 0 2em rgba(101, 189, 188, 0.3), 0 0 0 4em rgba(101, 189, 188, 0.3), 0 0 0 6em rgba(101, 189, 188, 0.3), 0 0 0 9em rgba(101, 189, 188, 0);
}
}



